import { Win } from '../data/constants'
type ErrorInfo = any;

// 监控页面错误
class ErrorTrace {
  private errorInfo: ErrorInfo;

  constructor() {
    this.errorInfo = {};
  }

  // 全局捕获 同步 + 异步 的错误
  private globalError() {
    console.log(`error: yasuo global error init`);
    Win.onerror = function (
      eventOrMessage: Event | string,
      scriptURL?: string,
      lineNum?: number,
      colNum?: number,
      error?: Error
    ){
      const data = JSON.stringify({
        scriptURL,
        lineNum,
        colNum,
        error
      })

      console.log(`[global error] ${eventOrMessage}`)

      console.log(data)

      // TODO: 错误上报

      // 防止爆红
      return true
    }
  }

  // 资源请求失败，比如图片 404
  private networkError() {
    Win.addEventListener(
      'error',
      function(e: ErrorEvent) {
        if (e.target !== Win) {
          console.log(`网络错误 ${e.target}`)
        }

        console.log(`网络错误 ${e.message}`)

        // TODO: 错误上报
      },
      true
    )
  }

  // 捕获promise的错误 (所有promise的reject都可以捕获到)
  private promiseError() {
    Win.addEventListener('unhandledrejection', function(e: PromiseRejectionEvent): boolean {
      e.preventDefault();

      console.log(`promise error ${e.reason}`)

      return true;
    })
  }

  // iframe 的报错，iframe 的报错，需要对每个 frame 单独去绑定
  private iframeError() {
    const frames = Win.frames;
    for (let i = 0; i < frames.length; i++){
      frames[i].addEventListener(
        'error',
        (e:ErrorEvent) => {
          console.log(`iframe error event ${e}`)
        },
        true
      )

      frames[i].addEventListener(
        'unhandledrejection',
        (e: PromiseRejectionEvent) => {
          console.log(`iframe error event ${e}`)
        },
        true
      )
    }
  }

  public run() {
    this.globalError();
    this.networkError();
    this.promiseError();
    // this.iframeError();
  }
}

export default ErrorTrace;